import React, { useState } from 'react'
import { makeStyles, IconButton, Typography } from '@material-ui/core';
import Fab from '@material-ui/core/Fab';
import MenuIcon from '@material-ui/icons/Menu';
import Drawer from '@material-ui/core/Drawer';
import CloseIcon from '@material-ui/icons/Close';
import colores from '../styles/colors';
import EjesModal from './ejesModal';
import ProyectosModal from './proyectModal'
import MyModal from './myModal'

const MobileMenu = () => {
    const classes = useStyles();
    const [openMenu, setOpenMenu] = useState(false);
	const [ejesHandler, setEjesHandler] = useState(false);
	const [proyectHandler, setProyectHandler] = useState(false);
	const [myHandler, setMyHandler] = useState(false);

    const mainButtons = [
		{
			//icon: <ShareIcon />,
			name: 'accion con Provincia',
			func: () => setMyHandler(true),
		},
		{
			//icon: <DashboardIcon />,
			name: 'ejes de campaña',
			func: () => setEjesHandler(true),
		},
		{
			//icon: <BookmarksIcon />,
			name: 'proyectos HCSF',
			func: () => setProyectHandler(true),
		},
	];
    console.log(ejesHandler);
    return <>
    <Fab onClick={() => setOpenMenu(true)}>
        <MenuIcon  />
    </Fab>
    <Drawer
        anchor='left'
        open={openMenu}
        //onClose={closeHandler}
    
    >
        <div 	className={classes.content}> 
            <IconButton
                className={classes.exitBtn}
                edge='end'
                color={colores.mainDarkfull}
               onClick={()=> setOpenMenu(false)}
                aria-label='close'
            >
                <CloseIcon />
            </IconButton>
         
                {mainButtons.map((btn) => (
                    <div className={classes.btnPr} onClick={()=> btn.func()}>
                        <Typography className={classes.textBtn}>{btn.name}</Typography>
                    </div>
                ))}
            
        </div>
    
    </Drawer>
    <EjesModal
				open={ejesHandler}
				handleClose={() => setEjesHandler(false)}
			/>
    <ProyectosModal open={proyectHandler} handleClose={() => setProyectHandler(false)}
			/>

    <MyModal open={myHandler} handleClose={() => setMyHandler(false)}
			/>
    </>
}

const useStyles = makeStyles(() => ({
    content: { width: '100%', height: '100%', backgroundColor: colores.fondoLight },
    exitBtn: {backgroundColor: colores.mainDark, margin: 15},
    list: {
       backgroundColor: 'red', alignSelf: 'center'
    },
    btnPr: {
        
       
        height: 80,
        backgroundColor: 'blue',
        
       
        marginTop: '25%',
        backgroundColor: 'red'
    },
    textBtn: {
        fontSize: 24,
        letterSpacing: 2,
        textAlign: 'center',
    }
}))

export default MobileMenu;